.swapBox{
    position:relative;
    .swapItem{
        background:#191919;
        border-radius:12px;
        padding:12px 15px;
        display:flex;
        justify-content:space-between;
        align-items:center;
        margin:0 0 10px;
        .view{
            flex:1;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            .label{
                color:#666;
                font-size: 14px;
                font-weight: 500;
                line-height: 1;
                margin:0 0 6px;
            }
            .price{
                color:#666;
                font-size: 12px;
                line-height: 1;
                margin:6px 0 0;
            }
            .inputWrap{
                position: relative;
                .input{
                    color:#fff;
                    :global(.adm-input-element){
                        color:#fff;
                        font-weight: 500;
                        font-size:22px;
                    }
                }
                .skeletonWrap{
                    position: absolute;
                    left:0;
                    top:0;
                    bottom:0;
                    right:0;
                    background:#191919;
                    .skeleton{
                        border-radius: 4px;
                        height:100%;
                    }
                }
            }
            input[type="number"]::-webkit-outer-spin-button,
            input[type="number"]::-webkit-inner-spin-button {
              -webkit-appearance: none;
              margin: 0;
            }
            input[type="number"] {
              -moz-appearance: textfield;
            }
        }
        .operation{
            flex:none;
            display:flex;
            flex-direction:column;
            justify-content:center;
            align-items:center;
            margin:0 0 0 10px;
            .type{
                display:flex;
                align-items:center;
                color:#fff;
                font-size:16px;
                background:#3b3b3c;
                border-radius:30px;
                padding:3px 10px 3px 7px;
                cursor:pointer;
                user-select:none;
                .icon{
                    width:28px;
                    height:28px;
                    margin:0 8px 0 0;
                }
                .arrow{
                    font-size: 14px;
                    margin:0 0 0 5px;
                }
            }
            .allBtn{
                font-size: 11px;
                color:#fff;
                background: #333;
                border-radius: 10px;
                padding:3px 7px;
                margin:5px 0 0;
                cursor: pointer;
            }
        }
    }
    .swapBtnWrap{
        position:absolute;
        left:50%;
        top:50%;
        margin:-18px 0 0 -18px;
    }
    .swapBtn{
        background: #a5bfe2;
        width:36px;
        height:36px;
        border-radius: 50%;
        display:flex;
        justify-content:center;
        align-items:center;
        cursor:pointer;
        svg{
            width:20px;
            height:20px;
        }
    }
}
.swapTip{
    display: flex;
    justify-content: center;
    font-size: 12px;
    color:#fff;
    line-height: 1;
    margin:10px 0 0;
}
.confirmBtn{
    margin:12px 0 0;
    background: #282930;
    border-color: #282930;
    color: #fff;
    font-weight:500;
    font-size:16px;
}
.confirmBtnError{
    margin:12px 0 0;
    background: #282930;
    border-color: #282930;
    color: #ff0000;
    font-weight:500;
    font-size:16px;
}
.selectWrap{
    height:100%;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    padding:20px 4.3vw 20px;
    background:#1f1f1f;
    .selectList{
        .item{
            display:flex;
            justify-content:space-between;
            align-items:center;
            background: #333;
            border-radius: 10px;
            padding:10px;
            margin:0 0 10px;
            cursor:pointer;
            user-select:none;
            &:last-child{
                margin:0;
            }
            .itemInfo{
                display:flex;
                align-items:center;
                .icon{
                    width:40px;
                    height:40px;
                    margin:0 8px 0 0;
                }
                .label{
                    .name{
                        color:#fff;
                        font-size: 15px;
                        margin:0 0 6px;
                    }
                    .balance{
                        color:#8d8d8d;
                        font-size: 14px;
                    }
                }
            }
            .itemPrice{
                color:#fff;
                font-size: 14px;
            }
        } 
    }
    .closeBtn{
        background: #333;
        border-radius: 15px;
        border-color: #333;
        font-weight: 500;
        margin:30px 0 0;
    }
}
.successModalWrap{
    background: #2a2a2a;
    .successModal{
        .description{
            color: #f1f1f1;
            text-align: center;
            word-break: break-all;
        }
        .link{
            display: flex;
            justify-content: center;
            margin: 10px 0 0;
            a{
                color: #1677ff;
                font-size: 16px;
            }
        }
    }
    :global(.adm-result) {
        padding: 12px 12px;
        background: transparent;
    }
    :global(.adm-result-success .adm-result-icon .antd-mobile-icon) {
        color: #52ab74;
    }
    :global(.adm-result-title) {
        color: #f1f1f1;
        font-weight: 500;
    }
    :global(.adm-modal-footer .adm-modal-button) {
        background: #333;
        border: none;
    }
}
.failModalWrap{
    background: #2a2a2a;
    .failModal{
        .description{
            color: #f1f1f1;
            text-align: center;
            word-break: break-all;
        }
    }
    :global(.adm-result) {
        padding: 12px 12px;
        background: transparent;
    }
    :global(.adm-result-success .adm-result-icon .antd-mobile-icon) {
        color: #52ab74;
    }
    :global(.adm-result-title) {
        color: #f1f1f1;
        font-weight: 500;
    }
    :global(.adm-modal-footer .adm-modal-button) {
        background: #333;
        border: none;
    }
}
.loadingWrap{
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(0,0,0,0.7);
    color: #fff;
    font-size: 16px;
    z-index: 9999;
    .text{
        margin: 15px 0 0;
    }
}
