.content{
    height:100%;
    display:flex;
    flex-direction:column;
    justify-content:space-evenly;
    .banner{
        display:flex;
        justify-content:center;
        svg{
            width:160px;
            height:auto;
        }
    }
    
}

.Private {
  .centerWrapper {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
  }

  .card {
    width: 16rem;           /* 64 */
    height: 3rem;           /* 16 */
    border-radius: 0.75rem; /* xl */
    border: 1px solid transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    will-change: transform, box-shadow, background, border-color;
    transition:
      background 220ms ease,
      border-color 220ms ease,
      box-shadow 220ms ease;
  }

  .selected {
    background: #2f3e63;
    border-color: #3b82f6;

    &:hover {
      background: #364775;
      border-color: #60a5fa;
      box-shadow: 0 0 16px rgba(59, 130, 246, 0.9);

      /* 進入「漸層文字」的過渡 */
      .label {
        background: linear-gradient(90deg, rgba(147, 197, 253, 0.9), rgba(224, 242, 254, 0.9));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }

  .unselectedTransparent {
    background: transparent;
    border-color: #9ca3af;

    &:hover {
      background: rgba(255, 255, 255, 0.05);
      border-color: #d1d5db;

      /* 60% → 80% 的文字亮度過渡 */
      .label {
        color: rgba(255, 255, 255, 0.8);
      }
    }
  }

  .glow {
    box-shadow: 0 0 12px rgba(59, 130, 246, 0.7);
  }

  .label {
    /* 預設 60% 透明度白色 */
    color: rgba(255, 255, 255, 0.6);
    font-weight: 700;
    font-size: 1.25rem; /* text-xl */

    /* 平滑的文字過渡：顏色與漸層背景 */
    transition:
      color 220ms ease,
      background 220ms ease,
      -webkit-text-fill-color 220ms ease;
  }

  /* 無障礙：使用者偏好減少動效時，關閉文字過渡 */
  @media (prefers-reduced-motion: reduce) {
    .card { transition: none; }
    .label { transition: none; }
  }
}